<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>My Subscription</p>
        </div>
        <div class="orderDetailWrap">
            <van-list
                v-model="listLoading"
                :finished="finished"
                loading-text='Loading...'
                @load="getData()"
            >
                <template v-if="listData.length > 0 || !listNoRecord">
                    <div class="whiteItemBox flexBetween" @click="goPage('/SubscriptionDetail/'+item.id)" v-for="(item,index) in listData" :key="index">
                        <div class="videoLeftBox flexStart">
                            <div class="statusLabel publishedStatus" v-if="item.status == 'subscribed'">Subscribed</div>
                            <div class="statusLabel pendingStatus" v-else-if="item.status == 'redeemable'">Redeemable</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'redeemed'">Redeemed</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'cancelled'">Cancelled</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'revoked'">Revoked</div>
                            <van-image
                                class='itemImg'
                                fit="contain"
                                :src="item.copyright.coverUrl"
                            />
                            <div class="videoMsgBox">
                                <p class="videoName van-multi-ellipsis--l2">{{item.copyright.title}}</p>
                                <p class="videoTime">Started on {{item.submitTime.split('/')[1]+'/'+item.submitTime.split('/')[2]}}</p>
                                <div class="numbersBox flexStart">
                                    <div class="viewsBox" style="width: 1.55rem; margin-right: .2rem;">
                                        <p class="videoNumber">{{moneySymbol + item.amount}}</p>
                                        <p class='numberTitle'>Amount</p>
                                    </div>
                                    <div class="viewsBox" style="width: 1.12rem; margin-right: .42rem;">
                                        <p class="videoNumber">{{parseFloat((item.interestRate*100).toFixed(2))+'%'}}</p>
                                        <p class='numberTitle'>Interest</p>
                                    </div>
                                    <div class="viewsBox" v-if="item.status == 'subscribed'">
                                        <p class="videoNumber">{{item.redeemAfter}}</p>
                                        <p class='numberTitle'>Redeem after</p>
                                    </div>
                                    <div class="viewsBox" v-else-if="item.status == 'redeemable'">
                                        <p class="videoNumber">{{item.redeemAfter}}</p>
                                        <p class='numberTitle'>Redeem after</p>
                                    </div>
                                    <div class="viewsBox" v-else-if="item.status == 'redeemed'">
                                        <p class="videoNumber">{{item.redeemTime}}</p>
                                        <p class='numberTitle'>Redeemed on</p>
                                    </div>
                                    <div class="viewsBox" v-else-if="item.status == 'cancelled'">
                                        <p class="videoNumber">{{item.redeemTime}}</p>
                                        <p class='numberTitle'>Cancelled on</p>
                                    </div>
                                    <div class="viewsBox" v-else-if="item.status == 'revoked'">
                                        <p class="videoNumber">{{item.redeemTime}}</p>
                                        <p class='numberTitle'>Revoked on</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
                <template v-else>
                    <div class="noRecordBox flexCenter">
                        <img class='noRecordIcon' src="../assets/images/noRecordIcon.png" alt="">
                        <p class="noRecordText">No record</p>
                    </div>
                </template>
            </van-list>
        </div>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'MySubscription',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                selectVideoTypeId: 'Created',

                listData: [],
                listLoading: false,
                finished: false,
                page: 1,
                listNoRecord: false,
			}
		},
		created(){
			// this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    page: this.page
                }
				https.fetchGet('/api/library/subscription/list',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.listData = this.listData.concat(res.data.list)
                        this.listLoading = false
                        this.page++
                        if(!res.data.hasNext) {
                            this.finished = true
                            console.log('finished')
                        }
                        if(this.listData.length == 0) {
                            this.listNoRecord = true
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
